n-color: #ffffff;\n    --sw-toolbar-btn-background-color: #009EF7;\n    --sw-anchor-default-primary-color: #f8f9fa;\n    --sw-anchor-default-secondary-color: #b0b0b1;\n    --sw-anchor-active-primary-color: #009EF7;\n    --sw-anchor-active-secondary-color: #ffffff;\n    --sw-anchor-done-primary-color: #90d4fa;\n    --sw-anchor-done-secondary-color: #fefefe;\n    --sw-anchor-disabled-primary-color: #f8f9fa;\n    --sw-anchor-disabled-secondary-color: #dbe0e5;\n    --sw-anchor-error-primary-color: #dc3545;\n    --sw-anchor-error-secondary-color: #ffffff;\n    --sw-anchor-warning-primary-color: #ffc107;\n    --sw-anchor-warning-secondary-color: #ffffff;\n    --sw-progress-color: #009EF7;\n    --sw-progress-background-color: #f8f9fa;\n    --sw-loader-color: #009EF7;\n    --sw-loader-background-color: #f8f9fa;\n    --sw-loader-background-wrapper-color: rgba(255, 255, 255, 0.7);\n}\n\n/* Base Styles */\n.sw {\n    position: relative;\n}\n\n.sw *,\n.sw *::before,\n.sw *::after {\n    box-sizing: border-box;\n}\n\n.sw > .tab-content {\n    position: relative;\n    overflow: hidden;\n}\n\n.sw > .tab-content > .tab-pane {\n    padding: 0.8rem;\n}\n\n.sw .toolbar {\n    padding: 0.8rem;\n    text-align: right;\n}\n\n.sw .toolbar > .sw-btn {\n    display: inline-block;\n    text-decoration: none;\n    text-align: center;\n    text-transform: none;\n    vertical-align: middle;\n    -webkit-user-select: none;\n    -moz-user-select: none;\n    user-select: none;\n    margin-left: 0.2rem;\n    margin-right: 0.2rem;\n    cursor: pointer;\n    padding: 0.375rem 0.75rem;\n    border-radius: 0.25rem;\n    font-weight: 400;\n    color: var(--sw-toolbar-btn-color);\n    background-color: var(--sw-toolbar-btn-background-color);\n    border: 1px solid var(--sw-toolbar-btn-background-color);\n}\n\n.sw .toolbar > .sw-btn.disabled, .sw .toolbar > .sw-btn:disabled {\n    opacity: 0.65;\n}\n\n.sw[dir=rtl] > .toolbar {\n    text-align: left;\n}\n\n.sw > .nav {\n    display: flex;\n    flex-wrap: w